<template>
    <MainLayout>
        <div class="settings-page">
            <div class="container">
                <div class="row">
                    <div class="col-md-12">
                        <div class="head-title">Settings</div>
                        <button type="button" class="sign-out btn btn-light" name="button" @click="logoutUser"><i class="material-icons">power_settings_new</i> வெளியேற</button>
                        <div class="settings-menu">
                            <a href="#" v-on:click="tabchange" class="active" data-tab="profile-settings">பக்கம்</a>
                            <a href="#" v-on:click="tabchange" data-tab="notification-settings">அறிவிப்புகள்</a>
                            <a href="#" v-on:click="tabchange" data-tab="password-settings">கடவுச்சொல்லை மாற்ற</a>
                        </div>
                        <div class="menu-contents">
                            <div class="tabs" id="profile-settings">
                                <form>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-first-name">முதற்பெயர் (தமிழில்) *</label>
                                            <!-- <input type="text" class="form-control" id="pratilipi-settings-first-name" :value="firstName" @input="updateFirstName" placeholder="முதற்பெயர் (தமிழில்)"> -->
                                            <TranslatingInput :value="firstName" :oninput="updateFirstName"></TranslatingInput>
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-last-name">கடைசி  பெயர் (தமிழில்)</label>
                                            <!-- <input type="text" class="form-control" id="pratilipi-settings-last-name" :value="lastName" @input="updateLastName" placeholder="கடைசி  பெயர் (தமிழில்)"> -->
                                            <TranslatingInput :value="lastName" :oninput="updateLastName"></TranslatingInput>
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-first-name-en">முதற்பெயர் (ஆங்கிலத்தில்) *</label>
                                            <input type="text" class="form-control" id="pratilipi-settings-first-name-en" :value="firstNameEn" @input="updateFirstNameEn" placeholder="முதற்பெயர் (ஆங்கிலத்தில்)">
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-last-name-en">கடைசி பெயர் (ஆங்கிலத்தில்)</label>
                                            <input type="text" class="form-control" id="pratilipi-settings-last-name-en" :value="lastNameEn" @input="updateLastNameEn" placeholder="கடைசி பெயர் (ஆங்கிலத்தில்)">
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-pen-name">புனைப்பெயர்  ( தமிழில் ) *</label>
                                            <input type="text" class="form-control" id="pratilipi-settings-pen-name" :value="penName" @input="updatePenName" placeholder="புனைப்பெயர்  ( தமிழில் )">
                                        </div>
                                        <div class="form-group">
                                            <label for="pratilipi-settings-language">மொழித்தேர்வு *</label>
                                            <select class="form-control" id="pratilipi-settings-language" @input="updateLanguage">
                                                <option disabled selected>மொழித்தேர்வு</option>
                                                <option :selected="eachLanguage.fullName.toUpperCase() === language" :value="eachLanguage.fullName.toUpperCase()" v-for="eachLanguage in constants.LANGUAGES" :key="eachLanguage.shortName">{{ eachLanguage.fullName.toUpperCase() }}</option>
                                            </select>
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-summary">உங்களைப் பற்றி ( விருப்பமிருப்பின் )</label>
                                            <textarea class="form-control" id="pratilipi-settings-summary" :value="summary" @input="updateSummary" rows="3"></textarea>
                                        </div>
                                    </div>
                                    <div class="section-title">Private Information</div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-email">மின்னஞ்சல் முகவரி</label>
                                            <input type="email" class="form-control" id="pratilipi-settings-email" :value="email" @input="updateEmail" placeholder="மின்னஞ்சல் முகவரி">
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-phone">தொலைபேசி</label>
                                            <input type="tel" class="form-control" id="pratilipi-settings-phone" :value="phone" @input="updatePhone" placeholder="தொலைபேசி">
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-gender">பாலினம்</label>
                                            <select class="form-control" id="pratilipi-settings-gender" @input="updateGender">
                                                <option disabled selected>பாலினம்</option>
                                                <option :selected="'MALE' === gender" value="MALE">ஆண்</option>
                                                <option :selected="'FEMALE' === gender" value="FEMALE">பெண்</option>
                                                <option :selected="'OTHER' === gender" value="OTHER">வேறு</option>
                                            </select>
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-dateOfBirth">பிறந்த தேதி</label>
                                            <input type="date" class="form-control" id="pratilipi-settings-dateOfBirth" :value="dateOfBirth" @input="updateDateOfBirth" placeholder="பிறந்த தேதி">
                                        </div>
                                    </div>
                                    <button type="button" class="btn btn-primary update-btn" @click="updateProfileSettings">மாற்றங்களை சேமிக்க</button>
                                </form>
                            </div>
                            <div class="tabs" id="notification-settings">
                                <form>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-email-frequency">மின்னஞ்சல் எப்போதெல்லாம் அனுப்பலாம்?</label>
                                            <select class="form-control" id="pratilipi-settings-email-frequency">
                                                <option disabled selected>மின்னஞ்சல் எப்போதெல்லாம் அனுப்பலாம்?</option>
                                                <option>உடனடியாக</option>
                                                <option>தினமும்</option>
                                                <option>வாரமொருமுறை</option>
                                                <option>மாதமொருமுறை</option>
                                                <option>எப்போதும் வேண்டாம்</option>
                                            </select>
                                        </div>
                                    </div>
                                    <div class="section-title">படைப்பு</div>
                                    <div class="form-row">
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_new_review">
                                            <label class="form-check-label" for="notif_option_new_review">புதிய விமர்சனம்</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_new_comment">
                                            <label class="form-check-label" for="notif_option_new_comment">புதிய கமெண்ட்</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_like_review">
                                            <label class="form-check-label" for="notif_option_like_review">விமர்சனத்திற்கு லைக்</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_like_comment">
                                            <label class="form-check-label" for="notif_option_like_comment">புதிய கமெண்டிற்கு லைக்</label>
                                        </div>
                                    </div>
                                    
                                    <div class="section-title">நெட்வொர்க்</div>
                                    <div class="form-row">
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_new_follower">
                                            <label class="form-check-label" for="notif_option_new_follower">புதிய ஃபாலோவர்</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_pratilipi_published_follower">
                                            <label class="form-check-label" for="notif_option_pratilipi_published_follower">நீங்கள் ஃபாலோ செய்பவரிடமிருந்து புதிய படைப்பு</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_pratilipi_updates">
                                            <label class="form-check-label" for="notif_option_pratilipi_updates">பிரதிலிபியின் அப்டேட்ஸ் மற்றும் சலுகைகள்</label>
                                        </div>
                                    </div>
                                    <button type="submit" class="btn btn-primary update-btn" disabled>மாற்றங்களை சேமிக்க</button>
                                </form>
                            </div>
                            <div class="tabs" id="password-settings">
                                <form>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-current-password">தற்போதைய கடவுச்சொல் *</label>
                                            <input type="password" class="form-control" v-model="oldPassword" id="pratilipi-settings-current-password" placeholder="தற்போதைய கடவுச்சொல்">
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-new-password">புதிய கடவுச்சொல் *</label>
                                            <input type="password" class="form-control" id="pratilipi-settings-new-password" placeholder="புதிய கடவுச்சொல்">
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-confirm-password">கடவுச்சொலை மீண்டும் பதிவு செய்க *</label>
                                            <input type="password" class="form-control" v-model="newPassword" id="pratilipi-settings-confirm-password" placeholder="கடவுச்சொலை மீண்டும் பதிவு செய்க">
                                        </div>
                                    </div>
                                    <button type="button" class="btn btn-primary update-btn" @click="updateUserPassword({ oldPassword, newPassword })">மாற்றங்களை சேமிக்க</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </MainLayout>
</template>

<script>
import MainLayout from '@/layout/main-layout.vue';
import TranslatingInput from '@/components/TranslatingInput.vue';
import constants from '@/constants';
import { mapGetters, mapActions, mapState } from 'vuex'

export default {
    components: {
        MainLayout,
        TranslatingInput
    },
    data() {
        return {
            constants,
            oldPassword: '',
            newPassword: ''
        }
    },
    computed: {
        ...mapGetters('settingspage', [
            'getAuthorData'
        ]),
        ...mapState({
            email: state => state.user.data.email,
            phone: state => state.user.data.phone,
            firstName: state => state.settingspage.author.data.firstName,
            language: state => state.settingspage.author.data.language,
            lastName: state => state.settingspage.author.data.lastName,
            firstNameEn: state => state.settingspage.author.data.firstNameEn,
            lastNameEn: state => state.settingspage.author.data.lastNameEn,
            penName: state => state.settingspage.author.data.penName,
            summary: state => state.settingspage.author.data.summary,
            gender: state => state.settingspage.author.data.gender,
            dateOfBirth: state => state.settingspage.author.data.dateOfBirth
        }),
        ...mapGetters([
            'getUserDetails',
            'getLogoutStatus'
        ]),
    },
    methods: {
        ...mapActions('settingspage', [
            'fetchAuthorDetails',
            'updateUserDetails',
            'updateAuthorDetails',
            'updateUserPassword'
        ]),
        ...mapActions([
            'logoutUser'
        ]),
        tabchange(event) {
            event.preventDefault();        
            var tab_id = $(event.currentTarget).attr('data-tab');
            $(".settings-menu a").removeClass("active");
            $(event.currentTarget).addClass("active");
            $(".tabs").hide();
            $("#" + tab_id).show();
        },
        updateEmail(e) { this.$store.commit('settingspage/updateEmail', e.target.value) },
        updatePhone(e) { this.$store.commit('settingspage/updatePhone', e.target.value) },
        updateFirstName(value) { this.$store.commit('settingspage/updateFirstName', value) },
        updateLanguage(e) { this.$store.commit('settingspage/updateLanguage', e.target.selectedOptions[0].value) },
        updateLastName(value) { this.$store.commit('settingspage/updateLastName', value) },
        updateFirstNameEn(e) { this.$store.commit('settingspage/updateFirstNameEn', e.target.value) },
        updateLastNameEn(e) { this.$store.commit('settingspage/updateLastNameEn', e.target.value) },
        updatePenName(e) { this.$store.commit('settingspage/updatePenName', e.target.value) },
        updateSummary(e) { this.$store.commit('settingspage/updateSummary', e.target.value) },
        updateGender(e) { this.$store.commit('settingspage/updateGender', e.target.selectedOptions[0].value) },
        updateDateOfBirth(e) { this.$store.commit('settingspage/updateDateOfBirth', e.target.value) },
        updateProfileSettings() {
            this.updateUserDetails();
            this.updateAuthorDetails();
        }
    },
    watch: {
        'getUserDetails.authorId'(newValue) {
            if (newValue) {
                this.fetchAuthorDetails(newValue);
            }
        },
        'getUserDetails.isGuest'(isGuest) {
            if (isGuest) {
                this.$router.push('login');
            }
        },
        'getLogoutStatus'(loggedOut) {
            if (loggedOut) {
                location.reload()    
            }
        }
    },
    created() {
        if (this.getUserDetails.isGuest) {
            this.$router.push('login');
        }

        if (this.getUserDetails.authorId) {
            this.fetchAuthorDetails(this.getUserDetails.authorId);    
        }
    }
}
</script>

<style lang="scss" scoped>
.settings-page {
    margin-top: 85px;
    text-align: left;
    @media screen and (max-width: 992px ) {
        margin-top: 65px;
    }
    .head-title {
        font-size: 18px;
        font-weight: bold;
        text-align: left;
        border-left: 3px solid #d0021b;
        padding-left: 10px;
        margin: 10px 0 0;
        float: left;
    }
    .sign-out {
        display: inline-block;
        color: #d0021b;
        float: right;
        margin-top: 10px;
        font-size: 14px;
        i {
            vertical-align: middle;
            font-size: 18px;
        }
    }
    .settings-menu {
        border-bottom: 1px solid #e9e9e9;
        padding: 8px 0 10px;
        text-align: left;
        overflow: hidden;
        width: 100%;
        overflow-x: auto;
        white-space: nowrap;
        clear: both;
        a {
            color: #555;
            font-size: 13px;
            border-bottom: 2px solid #fff;
            padding: 5px 5px 11px;
            span {
                font-size: 12px;
                font-weight: bold;
                padding: 0 2px;
                margin-right: 5px;
                display: inline-block;
                text-align: center;
            }
            &.active {
                color: #d0021b;
                border-color: #d0021b;
                span {
                    color: #d0021b;
                }
            }
            &:hover {
                text-decoration: none;
                color: #d0021b;
            }
        }
    }
    .tabs {
        margin: 20px 0;
        display: none;
        &#profile-settings {
            display: block;
        }
        label {
            font-size: 12px;
        }
        .section-title {
            font-size: 16px;
            margin: 10px 0;
            font-weight: bold;
        }
        .form-check {
            width: 100%;
            margin: 0 10px 5px 15px;
            label {
                font-size: 14px;
                vertical-align: text-bottom;
            }
        }
        .update-btn {
            background: #d0021b;
            border: 0;
            outline: none;
            font-size: 14px;
            float: right;
            margin: 10px 0;
        }
    }
}
</style>



