export default {
    
    getModalActionAndData: state =>  state,

    getModalTitle: state => {
        switch (state.heading) {
            case 'report_title':
                return 'ரிப்போர்ட் தலைப்பு';
            case 'edit_pratilipi_summary':
            	return 'படைப்பைப் பற்றி  (விருப்பமிருப்பின்) :';
            case 'edit_pratilipi_title':
            	return 'பெயர் *';
            case 'pratilipi_delete_content':
                return 'நீக்க';
        }
    },

    getInputModalSubheadings: state => {
    	switch (state.heading) {
            case 'report_title':
                return ['ரிப்போர்ட் தலைப்பு'];
            case 'edit_pratilipi_summary':
            	return ['படைப்பைப் பற்றி (விருப்பமிருப்பின்)'];
            case 'edit_pratilipi_title':
            	return ['தலைப்பை இங்கே எழுதவும்', 'தலைப்பை ஆங்கிலத்தில் எழுதவும்'];
            default:
            	return []
        }	
    },

    getModalMessage: state => {
        switch (state.message) {
            case 'pratilipi_confirm_delete_content':
                return 'படைப்பை நீக்கவேண்டுமா?';
        }
    }
}



